<template>
  <div class="video">
    <h3 class="title">YPlayer 视频播放器</h3>
    <div class="widget span6">
      <div class="widget-title">YPlayer</div>
      <div class="widget-body"> 
        <div :style="{width:width,height:height}" class="player"  id="playerName"></div>
      </div>
    </div>
    <div class="panel"></div>
  </div>
</template>
<script>
export default {
  name: "Video",
  data(){
    return {
      video_url: 'https://blz-videos.nosdn.127.net/1/OverWatch/AnimatedShots/Overwatch_AnimatedShot_Bastion_TheLastBastion.mp4',
      width: "800px",
      height: "450px",
      options: {
        tag_code: '',
        question_title_id: '',
        question_ids: [],
        preload: 'metadata',
        screenshot: false,
        theme: "#FDB550",
        playbackRates: [1.5, 1.25, 1, 0.75], // speed
        pictureinpicture: false,
        vertical: true,
        fullscreen: {
          full: true,
          fullweb: false
        },
        light: false,
        endpanel: {
          feedbackBtn: false,
          replayBtn:true
        },
        id: 'my-player',
        width: 665,
        height: 400,
        feedback: false,
        video: {
          url: 'https://blz-videos.nosdn.127.net/1/OverWatch/AnimatedShots/Overwatch_AnimatedShot_Bastion_TheLastBastion.mp4',
        },
        userInfo: {
          school_id: 0
        }
      }
    }
  },
  mounted(){
    this.$nextTick(() => {

        // if(this.videoData.id !== undefined) this.videoResource()
        if(this.options.id != undefined){
          this.options['container'] = document.getElementById('playerName')
          window.dplayer = new window.YPlayer(this.options, function () {});

        }
      })
  },
  methods: {
    initVideo () {
      console.log(document.getElementById("playerName"))
      this.$nextTick(() => {
        try {
          this.options = {
            container: document.getElementById("playerName"),
            preload: 'none',
            autoplay: false,
            video: {
              url: this.video_url,
              pic: 'https://file1.classba.cn/homepage.png',
              // thumbnails: 'https://i.loli.net/2019/06/06/5cf8c5d9cec8510758.jpg'
            },
            theme: '#FDB550',
            setting: false,
            playbackRates: [1.5, 1.25, 1, 0.75], // speed
            vertical: true,
            pictureinpicture: true,
            fullscreen: {
              full: true,
              fullweb: false
            },
            endpanel: {
              feedbackBtn: false,
              replayBtn: this.isReplay
            },
            userInfo: Object.assign({
              video_id: '',
              video_url: this.video_url,
            }),
            socket: this.is_scocket
          }
        } catch (e) {
          console.log({e})
        }
        const DEFAULT_EVENTS = [
          'loadeddata',
          'canplay',
          'canplaythrough',
          'play',
          'pause',
          'waiting',
          'playing',
          'ended',
          'error'
        ]
        const emitPlayerState = (event, value, player) => {
          if (event) {
            this.$emit(event, player)
          }
          if (value) {
            this.$emit(this.customEventName, { [event]: value })
          }
        }
        const self = this
        this.options.container = this.$refs[`${this.playerName}`]
        this.yplayer = new window.YPlayer(this.options, function () {
          // events
          const events = DEFAULT_EVENTS.concat(self.events)
          // watch events
          const onEdEvents = {}
          for (let i = 0; i < events.length; i++) {
            if (typeof events[i] === 'string' && onEdEvents[events[i]] === undefined) {
              (event => {
                onEdEvents[event] = null
                this.on(event, () => {
                  emitPlayerState(event, true, this)
                })
              })(events[i])
            }
          }
          self.$emit('ready', this)
        })
      })
    },
  }
}
</script>
<style scoped lang="scss">
  .video {
    .widget {
      width: 830px;
    }
  }
</style>


